.jumbotron {
  position: relative;
  overflow: hidden;
}


.jumbotron video {
  position: absolute;
  z-index: 1;
  top: 0;
  right:0;
  width:100%;
  height:100%;
  /*  object-fit is not supported on IE  */
  object-fit: cover;
  opacity:0.5;
}